import React, { Component } from 'react';
import classes from './DrawList.css';
import DrawItem from './DrawItem/DrawItem';


class DrawList extends Component {
    
        render() {
                let draws = <p style={{textAlign:'center'}}></p>
                
                        if (this.props.data) {
                            draws = this.props.data.map(draw => {
                                    let drawColor = ""
                                    let drawStyle = ""
                                    let drawBgColor = ""
                                    if (draw.status === "") {
                                        drawColor = "CompletedColor"
                                        drawStyle = "Completed"
                                        drawBgColor = "CompletedBgColor"
                                    } else if (draw.status === "") {
                                        drawColor = "OnGoingColor"
                                        drawStyle = "OnGoing"
                                        drawBgColor = "OnGoingBgColor"
                                    } else {
                                        drawColor = "NotStartColor"
                                        drawStyle = "NotStart"  
                                        drawBgColor = "NotStartBgColor"
                                    }
                                return <DrawItem marginTop={this.props.marginTop} 
                                marginBottom={this.props.marginBottom} 
                                title={this.props.title}
                                status={draw.status}
                                drawColor={drawColor}
                                drawStyle={drawStyle}
                                drawBgColor={drawBgColor}
                                scores={draw.scores}
                                teamSerialA={draw.teamSerialA}
                                teamSerialB={draw.teamSerialB}
                                teamA={draw.teamA}
                                teamB={draw.teamB}
                                resultA={draw.resultA}
                                resultB={draw.resultB}
                                key={draw.id}
                                />
                                
                            })
                            
                        };
                return (
                        <div className={classes.DrawList}>
                                {draws}
                        

                        </div>
                );
        }
}

export default DrawList;



