import React, { Component } from 'react';
import DrawControl from '../../components/DrawControl/DrawControl';
import DrawView from '../../components/DrawView/DrawView';
import classes from './DrawBuilder.css';
import axios from 'axios';
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';



class DrawBuilder extends Component {
    state = {
        showLine1: true,
        showLine2: false,
        roundID: 0,
        round1Choosed: {},
        round2Choosed: {},
        round3Choosed: {},
        rounds: [],
        controlStyle: ''
        
    };

    componentDidMount () {
        axios.get('https://next.json-generator.com/api/json/get/Vy8_msKMN')
            .then(response => {
                const roundsData = response.data;
                // console.log(roundsData)
                
                this.setState({
                    rounds: roundsData,
                    round1Choosed: roundsData[0].draws,
                    round2Choosed: roundsData[1].draws,
                    round3Choosed: roundsData[2].draws,
                    
                })
            })
            .catch(error => {
                this.setState({ error: true });
            
            });

        
            
    window.addEventListener('scroll', ()=> {
        // var header = this.refs.drawHeader;
        var scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
        if (scrollTop<117) {
            this.setState({
                controlStyle: 'Rel'
            })
        } else if (scrollTop>117) {
            this.setState({
                controlStyle: 'Pos'
                
            })

        }
    })  

    }
    

    clickedRound = (roundID) => {
        
        const length = this.state.rounds.length;
        const roundID2nd = roundID + 1;
        const roundID3rd = roundID + 2;
        this.setState({
            roundID: roundID
        })
    

        if (roundID2nd >= length) {
            this.setState({
                round1Choosed: this.state.rounds[roundID].draws,
                round2Choosed: {},
                round3Choosed: {}
            })
            return
        } if (roundID3rd >= length) {
            this.setState({
                round1Choosed: this.state.rounds[roundID].draws,
                round2Choosed: this.state.rounds[roundID2nd].draws,
                round3Choosed: {}
            })
            return
        } else {
            this.setState({
                round1Choosed: this.state.rounds[roundID].draws,
                round2Choosed: this.state.rounds[roundID2nd].draws,
                round3Choosed: this.state.rounds[roundID3rd].draws
            })
        }

        
    }

    printPDF = () => {
        
        const input = document.getElementById('divToExport');
        html2canvas(input)
            .then((canvas) => {
                const imgData = canvas.toDataURL('image/png');
                // const pdf = new jsPDF('', 'pt', 'a4');
                // pdf.addImage(imgData, 'JPEG', -150, 20, 1000, 1000 / canvas.width * canvas.height);
                const pdf = new jsPDF('', 'pt', 'a3');
                pdf.addImage(imgData, 'JPEG', -150, 30, 1150, 1150 / canvas.width * canvas.height);
                // pdf.output('dataurlnewwindow');
                pdf.save(".pdf");
            });
    }
 

    render() {
        return (
            <div className={classes.DrawBuilder}>
                <DrawControl 
                rounds={this.state.rounds}
                clickedRound={this.clickedRound}
                controlStyle={this.state.controlStyle}
                roundID={this.state.roundID}
                clickPdf={this.printPDF}
                />
                <div id="divToExport">
                <DrawView
                    round1={this.state.round1Choosed}
                    round2={this.state.round2Choosed}
                    round3={this.state.round3Choosed}
                    showLine1={this.state.showLine1}
                    showLine2={this.state.showLine2} />
                    </div>
            </div>
        );
    }
}

export default DrawBuilder;


