import React from 'react';
import classes from './RankItem.css';

const rankItem = (props) => (
    <div className={classes.RankItem}>
        <div className={classes.TeamSerial}>{props.teamSerial}</div>
        <div className={classes.TeamName}>{props.teamName}</div>
        <div className={classes.Ranking}>{props.ranking}</div>
        <div>{props.win}</div>
        <div>{props.lost}</div>
        <div>{props.winGame}</div>
        <div>{props.lostGame}</div>
    </div>
);

export default rankItem;


