import React from 'react';
import classes from './UnderLineButton.css';

const underLineButton = (props) => (
    <button className={[classes.UnderLineButton, classes[props.btnStyle]].join(' ')} onClick={props.clicked}>
        {props.name}
    </button>
);

export default underLineButton;


