import React, { Component } from 'react';
import classes from './CycleControl.css';
import UnderLineButton from '../UI/UnderLineButton/UnderLineButton';
import RoundButton from '../UI/RoundButton/RoundButton';

class CycleControl extends Component {
        render() {
                let buttons = <p style={{ textAlign: 'center' }}></p>

                if (this.props.groups) {
                        console.log(this.props.groups)
                        buttons = this.props.groups.map(button => {
                                let btnStyle = ''
                                if (button.id === this.props.groupID) {
                                        btnStyle = 'active'
                                } else {
                                        btnStyle = ''
                                }
                                console.log(button)
                                return <UnderLineButton
                                        name={button.title}
                                        key={button.id}
                                        btnStyle={btnStyle}
                                        clicked={() => this.props.clickedGroup(button.id)}
                                />

                        })

                };
                return (

                        <div className={classes.CycleControl}>
                                {buttons}
                                <div className={classes.Export}>
                                        <RoundButton
                                                name="pdf"
                                                clicked={this.props.clickPdf}
                                                btnType="Active"
                                        />
                                </div>
                        </div>
                );
        }
}

export default CycleControl;


