import React, { Component } from 'react';
import classes from './DrawControl.css';
import UnderLineButton from '../UI/UnderLineButton/UnderLineButton';
import RoundButton from '../UI/RoundButton/RoundButton';

class DrawControl extends Component {
        render() {
                let buttons = <p style={{ textAlign: 'center' }}></p>

                if (this.props.rounds) {
                        buttons = this.props.rounds.map(button => {
                                let btnStyle = ''
                                if (button.id === this.props.roundID) {
                                        btnStyle = 'active'
                                } else {
                                        btnStyle = ''
                                }
                                console.log(this.props.roundID)
                                return <UnderLineButton
                                        name={button.roundName}
                                        key={button.id}
                                        btnStyle={btnStyle}
                                        clicked={() => this.props.clickedRound(button.id)}
                                />

                        })

                };

                return (

                        <div className={[classes.DrawControl, classes[this.props.controlStyle]].join(' ')}>
                                {buttons}
                                <div className={classes.Export}>
                                        <RoundButton
                                                name="pdf"
                                                clicked={this.props.clickPdf}
                                                btnType="Active"
                                        />
                                </div>

                        </div>
                );
        }
}

export default DrawControl;


