import React, { Component } from 'react';
import classes from './Group.css';
import DrawItem from '../../DrawView/DrawList/DrawItem/DrawItem';
import RankItem from './RankItem/RankItem';

class Group extends Component {

    render() {
        let draws = <p style={{ textAlign: 'center' }}></p>

        if (this.props.draws) {
            draws = this.props.draws.map(draw => {
                let drawColor = ""
                let drawStyle = ""
                let drawBgColor = ""
                if (draw.status === "") {
                    drawColor = "CompletedColor"
                    drawStyle = "Completed"
                    drawBgColor = "CompletedBgColor"
                } else if (draw.status === "") {
                    drawColor = "OnGoingColor"
                    drawStyle = "OnGoing"
                    drawBgColor = "OnGoingBgColor"
                } else {
                    drawColor = "NotStartColor"
                    drawStyle = "NotStart"  
                    drawBgColor = "NotStartBgColor"
                }

                return <DrawItem marginTop='10px'
                    marginBottom='10px'
                    title={this.props.title}
                    status={draw.status}
                    drawColor={drawColor}
                    drawStyle={drawStyle}
                    drawBgColor={drawBgColor}
                    scores={draw.scores}
                    teamSerialA={draw.teamSerialA}
                    teamSerialB={draw.teamSerialB}
                    teamA={draw.teamA}
                    teamB={draw.teamB}
                    resultA={draw.resultA}
                    resultB={draw.resultB}
                    key={draw.id}
                />

            })

        };
        let ranks = <p style={{ textAlign: 'center' }}></p>


        if (this.props.ranks) {
            ranks = this.props.ranks.map(rank => {
                return <RankItem
                    ranking={rank.ranking}
                    teamSerial={rank.teamSerial}
                    teamName={rank.teamName}
                    win={rank.win}
                    lost={rank.lost}
                    winGame={rank.winGame}
                    lostGame={rank.lostGame}
                    key={rank.id}
                />

            })

        };

        return (
            <div className={classes.Group}>
                <div className={classes.Rank}>
                <header>{this.props.title}</header>
                <div className={classes.Container}>
                    <div className={classes.Title}>
                        <div></div>
                        <div className={classes.teamName}></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                    {ranks}
                </div>
                </div>  
                <div className={classes.DrawItems}>
                    {draws}
                </div>
                
            </div>
        );
    }
}

export default Group;


