import React from 'react';
import classes from './CycleView.css';
import Group from './Group/Group';

const cycleView = (props) => (
        <div className={classes.CycleView}>
            <Group 
            draws={props.draws}
            ranks={props.ranks}
            title={props.title}
            />
        </div>  
);

export default cycleView;


