import React from 'react';
import classes from './GameItem.css';

const gameItem = (props) => (
    <div className={classes.GameItem}>
        <div className={classes.Item}>{props.item}</div>
        <div className={[classes.Score,classes[props.bgColor]].join(' ')}>{props.scoreA}
            <div className={classes.TieBreak}>{props.tieBreakA}</div>
        </div>
        <div className={[classes.Score,classes[props.bgColor]].join(' ')}>{props.scoreB}
        <div className={classes.TieBreak}>{props.tieBreakB}</div>
        </div>
        
    </div>
);

export default gameItem;


