import React, { Component } from 'react';
import Layout from './components/Layout/Layout';
import DrawBuilder from './containers/DrawBuilder/DrawBuilder';
import CycleBuilder from './containers/CycleBuilder/CycleBuilder';
import Filter from './containers/Filter/Filter';
import classes from './App.css';


class App extends Component {
  state = {
    CycleShow: '',
    DrawShow: 'Hide',
    CycleBtnStyle: 'Active',
    DrawBtnStyle: ''
  }

  showCycle = () => {
    this.setState ({
      CycleShow: '',
      DrawShow: 'Hide',
      CycleBtnStyle: 'Active',
      DrawBtnStyle: ''
    })
  }

  showDraw = () => {
    this.setState ({
      CycleShow: 'Hide',
      DrawShow: '',
      CycleBtnStyle: '',
      DrawBtnStyle: 'Active'
    })
  }

  render() {
    return (
      
      <div className={classes.App}>
        <Layout>
          <Filter 
          showCycle={this.showCycle}
          showDraw={this.showDraw}
          cycleBtnStyle={this.state.CycleBtnStyle}
          drawBtnStyle={this.state.DrawBtnStyle}/>

          <div className={classes[this.state.CycleShow]}><CycleBuilder /></div>
          <div className={classes[this.state.DrawShow]}><DrawBuilder /></div>
          
          
        </Layout>
      </div>
    );
  }
}

export default App;



