import React, { Component } from 'react';
import classes from './DrawItem.css';
import GameItem from './GameItem/GameItem';
// import check from '../../../../assets/images/check@2x.png';

class DrawItem extends Component {
    
    render() {
        let score = <p style={{textAlign:'center'}}>0</p>
        if (this.props.scores) {
            score = this.props.scores.map(score => {
                return <GameItem 
                item={score.gameItem}
                bgColor={this.props.drawBgColor}
                key={score.id}
                scoreA={score.scoreA}
                scoreB={score.scoreB}
                tieBreakA={score.tieBreakA}
                tieBreakB={score.tieBreakB}
                />
            })
        };
        return (

            <div className={[classes.DrawItem,classes[this.props.drawStyle]].join(' ')} style={{ marginTop: this.props.marginTop, marginBottom: this.props.marginBottom }}>
                <header>
                    <div className={classes.Status}>{this.props.status}</div>
                {this.props.title}</header>
                <div className={classes.Container}>
                <div className={classes.Serial}>
                    <div className={[classes.SerialA,classes[this.props.drawColor]].join(' ')}>{this.props.teamSerialA}</div>
                    <div className={[classes.SerialB,classes[this.props.drawColor]].join(' ')}>{this.props.teamSerialB}</div>
                </div>
                    <div className={classes.Teams}>
                        <div className={classes.TeamA}>{this.props.teamA}</div>
                        <div className={classes.TeamB}>{this.props.teamB}</div>
                    </div>
                    <div className={classes.Result}>
                        {/* <img src={check} alt="" /> */}
                        <div className={classes.ResultA}>{this.props.resultA}</div>
                        <div className={classes.ResultB}>{this.props.resultB}</div>
                    </div>
                    <div className={classes.Scores}>
                        {score}
                    </div>
                </div>
            </div>
        )
    }

};

export default DrawItem;


