import React, { Component } from 'react';
import classes from './CycleBuilder.css';
import CycleControl from '../../components/CycleControl/CycleControl';
import CycleView from '../../components/CycleView/CycleView';
import axios from 'axios';
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';

class CycleBuilder extends Component {

    state = {
        groups: [],
        draws: [],
        ranks: [],
        title: '',
        groupID: 0
    }

    componentDidMount () {
        axios.get('https://next.json-generator.com/api/json/get/N1n8c9tMN')
            .then(response => {
                const groupsData = response.data;
                
                this.setState({
                    groups: groupsData,
                    draws: groupsData[0].matchs,
                    ranks: groupsData[0].rank,
                    title: groupsData[0].title
                })
            })
            .catch(error => {
                this.setState({ error: true });
            
            });

    }
    clickedGroup = (groupID) => {
        
        this.setState({
            draws: this.state.groups[groupID].matchs,
            ranks: this.state.groups[groupID].rank,
            title: this.state.groups[groupID].title,
            groupID: groupID
        })
    }

    printPDF = () => {
        
        const input = document.getElementById('divToPrint');
        html2canvas(input)
            .then((canvas) => {
                const imgData = canvas.toDataURL('image/png');
                const pdf = new jsPDF('', 'pt', 'a3');
                pdf.addImage(imgData, 'JPEG', -150, 30, 1150, 1150 / canvas.width * canvas.height);
                // pdf.output('dataurlnewwindow');
                pdf.save(".pdf");
            });
    }

    render () {
        return(
            <div className={classes.CycleBuilder}>
                <CycleControl 
                groups={this.state.groups}
                clickedGroup={this.clickedGroup}
                groupID={this.state.groupID}
                clickPdf={this.printPDF}
                />
                <div id="divToPrint">
                <CycleView 
                draws={this.state.draws}
                ranks={this.state.ranks}
                title={this.state.title}
                />
                </div>
            </div>
        );
    }
}

export default CycleBuilder;


