import React from 'react';
import classes from './DrawLine.css';

const drawLine = (props) => (

    props.show ?
        
            
            <div className={classes.DrawLine}>


                <svg width="31px" height="154px" viewBox="0 0 31 154" version="1.1">
                    <g id="Welcome" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                        <g id="Desktop-HD" transform="translate(-539.000000, -305.000000)" strokeWidth="2" stroke="#088CCB">
                            <g id="line" transform="translate(539.000000, 306.000000)">
                                <polyline id="Path-2" points="0 0 13.125 0 13.125 152 0 152"></polyline>
                                <path d="M13.4375,75.5 L29.6875,75.5" id="Line" strokeLinecap="square"></path>
                            </g>
                        </g>
                    </g>
                </svg>
            </div>
         :
        
        
        <div className={classes.DrawLine2}>

            <svg width="31px" height="300px" viewBox="0 0 31 300" version="1.1">

                <g id="Welcome" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                    <g id="Desktop-HD" transform="translate(-908.000000, -382.000000)" strokeWidth="2" stroke="#088CCB">
                        <g id="line2" transform="translate(908.000000, 383.000000)">
                            <polyline id="Path-2-Copy-3" points="0 0 13.125 0 13.125 298 0 298"></polyline>
                            <path d="M13.4375,149.5 L29.6875,149.5" id="Line-Copy-3" strokeLinecap="square"></path>
                        </g>
                    </g>
                </g>
            </svg>

        </div>
        

);



export default drawLine;


