import React, { Component } from 'react';
import classes from './LineList.css';
import DrawLine from '../DrawLine/DrawLine';


class LineList extends Component {
        
        render() {
                let lines = <p style={{textAlign:'center'}}></p>
               
                        if (this.props.data) {
                            lines = this.props.data.map(line => {
                                return <DrawLine show={this.props.line}/>
                                   
                            })
                            
                        };
                return (
                        <div>
                        <div className={classes.Space}></div>
                                {lines}
                        </div>
                );
        }
}

export default LineList;



