import React from 'react';
import classes from './DrawView.css';
import DrawList from './DrawList/DrawList';
import LineList from './LineList/LineList';

const drawView = (props) => (
    <div className={classes.DrawView}>
        <div>
            <DrawList data={props.round1.matchs}
                title={props.round1.title}
            />
        </div>
        <div className={classes.Second}>
            <LineList 
            data={props.round2.matchs}
            line={props.showLine1}/>
        </div>
        <div className={classes.Second}>
            <DrawList
                marginTop='93px'
                marginBottom='167px'
                data={props.round2.matchs}
                title={props.round2.title}
            />
        </div>
        <div className={classes.Third}>
            <LineList 
            data={props.round3.matchs}
            line={props.showLine2}/>
        </div>
        <div className={classes.Third}>
            <DrawList
                marginTop='242px'
                marginBottom='465px'
                data={props.round3.matchs}
                title={props.round3.title}
            />
        </div>

    </div>
);

export default drawView;


